/* styles.css */
.welcome-section, .help-section, .christmas-section, .gift-section {
    /* Common styles for sections */
    padding: 20px;
    margin: 20px;
    border-radius: 10px;
  }
  
  /* Specific styles for each section */
  .welcome-section { background-color: lightblue; }
  .help-section { background-color: lightgreen; }
  .christmas-section { background-color: lightcoral; }
  .gift-section { background-color: lightyellow; }

  /* styles.css */
.fullscreen-section {
  min-height: 100vh; /* 100% of the viewport height */
  width: 100%; /* Full width */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* Add more styles as needed */
}

/* src/components/LoadingAnimation.css */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  width: 100vw; /* Full viewport width */
}

.loading-spinner {
  width: 100px; /* Adjust size as needed */
  height: 100px; /* Adjust size as needed */
  animation: spin 3s ease-out; /* Adjust duration and easing as needed */
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}